export default ()=> {
      return (
        <div className="question" data-index="60" dangerouslySetInnerHTML = {{ __html: `
<h6>60. 输出是什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> user <span class="token operator">=</span> <span class="token punctuation">{</span> name<span class="token operator">:</span> <span class="token string">"Lydia"</span><span class="token punctuation">,</span> age<span class="token operator">:</span> <span class="token number">21</span> <span class="token punctuation">}</span><span class="token punctuation">;</span>
<span class="token keyword keyword-const">const</span> admin <span class="token operator">=</span> <span class="token punctuation">{</span> admin<span class="token operator">:</span> <span class="token boolean">true</span><span class="token punctuation">,</span> <span class="token operator">...</span>user <span class="token punctuation">}</span><span class="token punctuation">;</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>admin<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>{ admin: true, user: { name: &quot;Lydia&quot;, age: 21 } }</code></li>
<li>B: <code>{ admin: true, name: &quot;Lydia&quot;, age: 21 }</code></li>
<li>C: <code>{ admin: true, user: [&quot;Lydia&quot;, 21] }</code></li>
<li>D: <code>{ admin: true }</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>扩展运算符<code>...</code>为对象的组合提供了可能。你可以复制对象中的键值对，然后把它们加到另一个对象里去。在本例中，我们复制了<code>user</code>对象键值对，然后把它们加入到<code>admin</code>对象中。<code>admin</code>对象就拥有了这些键值对，所以结果为<code>{ admin: true, name: &quot;Lydia&quot;, age: 21 }</code>.</p>
</p>
</details>
` }}></div>
      );
    }